import React from "react";
import {
  Nav,
  NavItem,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import { NavLink } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import * as Icon from "react-feather";
import Logo from './assets/img/logo/logo-dark2.png';

const tabs = [
  {
    route: "/assets/add",
    icon: <Icon.DollarSign size={20} className="mr-50" />,
    label: "Add Assets",
  },
  {
    route: "/liability/add",
    icon: <Icon.Command size={20} className="mr-50" />,
    label: "Add Liabilties",
  },
  {
    route: "/login",
    icon: <img src={Logo} style={{width:'30px',height:'20px', transform: 'scale(1.5)'}} />,
    label: "Login",
  },
  {
    route: "/portfolio",
    icon: <Icon.Briefcase size={20} className="mr-50" />,
    label: "View Portfolio",
  },
  {
    route: "/nominee/list",
    icon: <Icon.Users size={20} className="mr-50" />,
    label: "Manage Nominees",
  },
];

const MyNav = (props) => {
  return (
    <div>
      {/* <nav className="navbar navbar-expand-md navbar-light sticky-top" role="navigation">
        <div className="container-fluid">
            <a className="navbar-brand" href="/home">Brand</a>
            <Nav className="ml-auto">
              <NavItem>
                <NavLink to="/search" className="nav-link">
                  Search
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink to="/login" className="nav-link">
                  Login
                </NavLink>
              </NavItem>
            </Nav>
        </div>
      </nav> */}

      <nav
        className="navbar fixed-bottom navbar-dark"
        role="navigation"
        style={{ backgroundColor: "#21212a" }}
      >
        <Nav className="w-100" style={{ backgroundColor: "#21212a" }}>
          <div
            className=" d-flex flex-row justify-content-around w-100"
            style={{ backgroundColor: "#21212a" }}
          >
            {tabs.map((tab, index) => {
              if (tab.label === "Login") {
                return (
                  <NavItem key={`tab-${index}`}>
                    <UncontrolledDropdown nav inNavbar direction="up">
                      <DropdownToggle nav caret>
                        {tab.icon}
                      </DropdownToggle>
                      <DropdownMenu
                        flip
                        left
                        style={{
                          width: "300px",
                          marginLeft: "-128px",
                          border: "solid 1px",
                        }}
                      >
                        <DropdownItem>Option 1</DropdownItem>
                        <DropdownItem>Option 2</DropdownItem>
                        <DropdownItem divider />
                        <DropdownItem>Log Out</DropdownItem>
                      </DropdownMenu>
                    </UncontrolledDropdown>
                  </NavItem>
                );
              } else {
                return (
                  <NavItem key={`tab-${index}`}>
                    <NavLink
                      to={tab.route}
                      className="nav-link"
                      activeClassName="active"
                    >
                      <div className="row d-flex flex-column justify-content-center align-items-center">
                        {/* <FontAwesomeIcon size="lg" icon={tab.icon}/> */}
                        {tab.icon}

                        {/* <div>{tab.label}</div> */}
                      </div>
                    </NavLink>
                  </NavItem>
                );
              }
            })}
          </div>
        </Nav>
      </nav>
    </div>
  );
};
export default MyNav;
